<template>
  <!-- 右侧内容 -->
  <div class="order-right">
    <div class="order-content">
      <div class="title">
        <h3>我的订单</h3>
      </div>
      <div class="chosetype">
        <table>
          <thead>
            <tr>
              <th width="29%">商品</th>
              <th width="31%">订单详情</th>
              <th width="13%">收货人</th>
              <th>金额</th>
              <th>状态</th>
              <th>操作</th>
            </tr>
          </thead>
        </table>
      </div>
      <div class="orders" v-for="item in orderList.records" :key="item.id">
        <table class="order-item">
          <thead>
            <tr>
              <th colspan="5">
                <span class="ordertitle"
                  >{{ item.createTime }}　订单编号：{{ item.outTradeNo }}
                  <span class="pull-right delete"
                    ><img src="../images/delete.png" /></span
                ></span>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(goods, inedex) in item.orderDetailList" :key="goods.id">
              <td width="60%">
                <div class="typographic">
                  <img
                    :src="goods.imgUrl"
                    style="width: 100px; height: 100px"
                  />
                  <a href="#" class="block-text">{{ goods.skuName }}</a>
                  <span>x{{ goods.skuNum }}</span>
                  <a href="#" class="service">售后申请</a>
                </div>
              </td>
              <td
                :rowspan="item.orderDetailList.length"
                v-if="inedex == 0"
                width="8%"
                class="center"
              >
                {{ item.consignee }}
              </td>
              <td
                :rowspan="item.orderDetailList.length"
                v-if="inedex == 0"
                width="13%"
                class="center"
              >
                <ul class="unstyled">
                  <li>总金额¥{{ item.totalAmount }}.00</li>
                  <li>在线支付</li>
                </ul>
              </td>
              <td
                :rowspan="item.orderDetailList.length"
                v-if="inedex == 0"
                width="8%"
                class="center"
              >
                <a href="#" class="btn">{{ item.orderStatusName }} </a>
              </td>
              <td
                :rowspan="item.orderDetailList.length"
                v-if="inedex == 0"
                width="13%"
                class="center"
              >
                <ul class="unstyled">
                  <li>
                    <a href="mycomment.html" target="_blank">评价|晒单</a>
                  </li>
                </ul>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="choose-order">
        <div class="pagination">
          <!-- 分页器 -->
          <Pagination
            :pageNo="page"
            :pageSize="limit"
            :total='orderList.total'
            :continues="5"
            @getPageNo="getPageNo"
          />
        </div>
      </div>
    </div>
    <!--猜你喜欢-->
    <div class="like">
      <h4 class="kt">猜你喜欢</h4>
      <ul class="like-list">
        <li class="likeItem">
          <div class="p-img">
            <img src="../images/itemlike01.png" />
          </div>
          <div class="attr">
            <em>DELL戴尔Ins 15MR-7528SS 15英寸 银色 笔记本</em>
          </div>
          <div class="price">
            <em>¥</em>
            <i>3699.00</i>
          </div>
          <div class="commit">已有6人评价</div>
        </li>
        <li class="likeItem">
          <div class="p-img">
            <img src="../images/itemlike02.png" />
          </div>
          <div class="attr">Apple苹果iPhone 6s/6s Plus 16G 64G 128G</div>
          <div class="price">
            <em>¥</em>
            <i>4388.00</i>
          </div>
          <div class="commit">已有700人评价</div>
        </li>
        <li class="likeItem">
          <div class="p-img">
            <img src="../images/itemlike03.png" />
          </div>
          <div class="attr">DELL戴尔Ins 15MR-7528SS 15英寸 银色 笔记本</div>
          <div class="price">
            <em>¥</em>
            <i>4088.00</i>
          </div>
          <div class="commit">已有700人评价</div>
        </li>
        <li class="likeItem">
          <div class="p-img">
            <img src="../images/itemlike04.png" />
          </div>
          <div class="attr">DELL戴尔Ins 15MR-7528SS 15英寸 银色 笔记本</div>
          <div class="price">
            <em>¥</em>
            <i>4088.00</i>
          </div>
          <div class="commit">已有700人评价</div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "MyOrder",
  data() {
    return {
      // 初始化获取 列表信息 请求参数
      // 默认请求第一页
      page: 1,
      // 默认一页三条信息
      limit: 3,
      orderList: {},
    };
  },
  methods: {
    // 将派发请求 封装为函数 即用即调
    async getListData() {
      let result = await this.$API.reqMyOrderList(this.page, this.limit);
      console.log(result);
      if (result.code == 200) {
        this.orderList = result.data;
      } else {
        alert("请求订单列表失败");
      }
    },
    // 随页面变动请求相应数据
    getPageNo(page){
      this.page = page
      this.getListData();
    }
  },
  mounted() {
    // 挂载完毕派发请求
    this.getListData();
  },
};
</script>

